
//先針對media queries的width設為變數
$pc-width: 100%;
$pad-width: 672px;
$phone-width: 352px;
$iPhoneSE-width: 100%;

@mixin pc{
    @media (max-width:1200px){
      @content
    }
};

@mixin pad{
    @media (max-width:992px){
      @content
    }
};

@mixin iPhone{
    @media (max-width:414px){
      @content
    }
};



/* container 顯示在出來 
----------------------- */

.content{
    max-width: $pc-width;
    margin: 0 auto;
}

.content{
    @include pad {
        max-width: $pad-width;
        width: 100%;
        margin: 0 auto;
    }
}

.content{
    @include iPhone {
        max-width: $phone-width;
        margin: 0 auto;
    };
}

