
/* _util 工具 
----------------------- */

img {
    max-width: 100%;
    height: auto;
    display: block;
}

  
// bg-cover ------------------ /

.bg-cover{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}
.bg-contain{
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}


// container ------------------- /

.container-inner{
    width: 75%;
    margin: 0 auto;
}

.content{
    padding: 0 25px;
}


// display --------------------- /
.display-block{
    display: block;
}

.display-in-block{
    display: inline-block;
}


// position --------------------- /
.position-r{
    position: relative;
}


// font-align  --------------------- /
.font-align {
    text-align: center;
}

.font-align-left {
    text-align: left;
}

.font-align-right {
    text-align: right;
}

.font-weight-medium{
    font-weight: 400;
}

.font-weight-big{
    font-weight: 500;
}


// m -* ------------------------------/
.mb-xl{
    margin-bottom: 32px;
}
.mb-md{
    margin-bottom: 24px;
}

.mb-sl{
    margin-bottom: 22px;
}
.mb-ssl{
    margin-bottom: 12px;
}
// m -r
.mr-xl{
    margin-right: 32px;
}
.mr-md{
    margin-right: 24px;
}
.mr-sl{
    margin-right: 22px;
}
.mr-ssl{
    margin-right: 12px;
}
