
.wrap {
    display: flex;
    height: 100vh;
}
nav.header{
    height: 93px;
    padding-top: 24px;
    h2, p{
        margin-bottom: 0;
    }
}
.sidebar {
    position: fixed; //固定位置
    top:0;
    bottom:0;
    width: 200px;
    background: $background-color-website;
    padding: 32px 0 32px 32px;
    min-width: 296px;
    background-color: #edf4ee;
    li {
        margin-bottom: 29px;
        cursor: pointer;

        & i {
            margin-right: 16px;
            vertical-align: bottom;
        }
    }
    .purson {
        p{
            margin-bottom: 4px;
            &:last-child {
                color: $color-gray;
                font-size: $font-size-xxs;
            }
        }
        img{
            width: 40px;
            height: 40px;
            border-radius: 50%;
        }
    }
        
    
}

// 獨立
.list-group-item-action { color: $color-back;}
.font-size-xxs { font-size: $font-size-xxs!important;}
.border-radius-none { border-radius: 0px;}

div span{ 
    display: inline-block;
    background-color: #edf4ee;
    color: #2a8356;
    padding: 0 4px;
    text-align: center;
    font-weight: 600;
}

.main{
    margin-left: 296px;
    padding: 0 32px 32px 32px;
    .content {
        padding: 0 0px;
        .allComment p{
            text-align: left;
            margin-bottom: 0rem;
            font-size: $font-size-xs;
            font-weight: bold;
        }
        .comment p{
            text-align: right;
            font-size: $font-size-double-xxs;
            color: $color-four;
            margin-bottom: 0rem;
            line-height: 2.2em;
        }
        .card-body p.contentBox-colorGray{
            color: $color-four;
        }
        button{
            background-color: $background-color-headles;
            border: 1px solid $border-headles;
            font-size: 16px;
            padding: 8px 32px;
            color: #fff;
            font-weight: 500;
            border-radius: 5px;
            color: $color-white;
            .buttonHandler{
                background-color: $background-color-headles;
                border: 1px solid $border-headles;
            }
            &:hover{
                background-color: transparent;
                border: 1px solid $border-headles;
                color: $border-headles;
            }
        }
        .card-header {
            color: initial;
            border-color: $border-website;
            background-color: $background-color-website;
        }
        .list-group-item{ 
            border: 1px solid $border-website;
            padding: 12px 12px;
            &:last-child,
            &:first-child{
                border-bottom-right-radius: 0rem;
                border-bottom-left-radius: 0rem;
                border-top-left-radius: 0rem;
                border-top-right-radius: 0rem;
            }
            &.active {
                color: initial;
                border-color: $border-website;
                background-color: $background-color-website;
            }
            li p{
                font-size: 12px;
                margin-bottom: 4px;
                &.font-size-xs{
                    font-size: 16px!important;
                }
                
            }
            li:last-child p{
                margin-bottom: 8px;
                &.fontLight {
                    color: #0000009A;
                }
            }
        }
        .tab-content .card.list-group-item {
            padding: 0 0rem;
            .media-body {
                img{
                    width: 40px;
                    height: 40px;
                    border-radius: 50%;
                }
                .media-title {
                    h4{ font-size: $font-size-xxs;}
                    h5{ font-size: $font-size-double-xxs;}
                }
                .media-body-item{
                    padding-left: 58px;
                    p, ol{
                        font-size: $font-size-xxs;
                        li{
                            padding-left: 16px;
                        }
                    }
                }
                button{
                    background-color: $color-website;
                    border: 1px solid $border-website;
                    line-height: 40px;
                    padding-left: 12px;
                    padding-right: 16px;
                    font-weight: bold;
                    margin-right: 8px;  
                    border-radius: 0px;
                    color: $color-back;
                }
            }
        }
    }
    
    
}
