/* _base 全站設定  
----------------------- */
*, *::before, *::after {
    box-sizing: border-box;
}
  
html,body {
    font-family: 'Lato', sans-serif;
    //font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Microsoft JhengHei", Roboto, "Helvetica Neue", Arial, sans-serif;
    // font-weight: 400;
}

body{
    font-size: 16px;
    line-height: 1.5; 
    /* 初始值 16*1.5 = 24px */
}

img {
    max-width: 100%;
    height: auto;
    display:block;
}

a {
    text-decoration: none;
    color: inherit;
}

a:hover {
    color: #2A8356;
}

dl, ol, ul{
    list-style-type: none;
    padding: 0 0;
    margin: 0 0;
}


