// font  ------------------ /  
h1, h2, h3 {
    font-weight: $font-width-double;
}

.h1 {
    font-size: $font-size-xxl;
    margin-bottom: 32px;
}
.h2{
    font-size: $font-size-xl;
    margin-bottom: 60px;
    font-style: normal;
}
h3 { 
    font-size: $font-size-md;
    margin-bottom: 4px;
}



// from ------------------ /
.label-wrap{
    width: 100%;
    margin: 0 auto;
}
.label-contact-group{
    margin-bottom: 16px;
}
input[type="text"], textarea{
    width: 100%;
    border: $border-secondary;
    background: transparent;
    margin-top: 4px;
    padding: 14px 16px;
}
::placeholder, input[type="submit"], label{
    font-size: 16px;
    line-height: 24px;
}
label input[type="checkbox"]  {
    display: inline-block;
    cursor: pointer;
    margin-right: 4px;
}
.submit{
    margin-top: 42px;
    text-align: center;
}
input[type="submit"]{
cursor: pointer;
    background: $color-primary; //可似情況更改
    color: #fff;
    // padding: 16px 58px; //可似情況更改
    border: $border-primary; //可似情況更改
}
input[type="submit"]:hover{
    background: $color-secondary; //可似情況更改
}

// container - main ------------------ /
.main-inner{
    padding: 64px 0 80px;
}